<template>
 <main class="content">
				<div class="container-fluid p-0">
<div class="page-title-right">
        <ol class="breadcrumb m-0">
          <li class="breadcrumb-item">
            <a href="javascript: void(0);">Anasayfa</a>
          </li>
          <li class="breadcrumb-item active">BomGir</li>
        </ol>
      </div>
      <br />
					<h1 class="h3 mb-3">Profil Ayarları</h1>

					<div class="row">
						<div class="col-md-3 col-xl-2">

							<div class="card">
								<div class="card-header">
									<h5 class="card-title mb-0">Profil Ayarları</h5>
								</div>

								<div class="list-group list-group-flush" role="tablist">
									<a class="list-group-item list-group-item-action active" data-toggle="list" href="#account" role="tab" aria-selected="false">
          Kullanıcı Bilgileri
        </a>
									<a class="list-group-item list-group-item-action" data-toggle="list" href="#password" role="tab" aria-selected="false">
          Şifre
        </a>
			
								</div>
							</div>
						</div>

						<div class="col-md-9 col-xl-10">
							<div class="tab-content">
								<div class="tab-pane active show" id="account" role="tabpanel">

									<div class="card">
										<div class="card-header">
										
											<h5 class="card-title mb-0">Genel Bilgiler
                                            </h5>
										</div>
										<div class="card-body">
											<form>
												<div class="row">
													<div class="col-md-8">
														<div class="form-group">
															<label for="inputUsername">Kullanıcı Adı: </label>
															<input type="text" class="form-control" id="inputUsername" v-model="UserName" placeholder="Kullanıcı Adı">
														</div>
														<div class="form-group">
															<label for="inputUsername">Detay Bilgi</label>
															<textarea rows="2" class="form-control" v-model="Biyografi" placeholder="Detay Bilgi"></textarea>
														</div>
													</div>
													<div class="col-md-4">
														<div class="text-center">
															<img alt="Chris Wood" src="/images/default_user.jpg" class="rounded-circle img-responsive mt-2" width="128" height="128">
															<div class="mt-2">
																<span class="btn btn-primary"><i class="fas fa-upload"></i> Yükle</span>
															</div>
															<small>Dosya Boyutu 1 MB' dan az olmalı</small>
														</div>
													</div>
												</div>

												<button type="button" class="btn btn-primary" @click="savegenel">Değişikleri Kaydet</button>
											</form>

										</div>
									</div>

									<div class="card">
										<div class="card-header">
									    <h5 class="card-title mb-0">Özel Bilgiler</h5>
										</div>
										<div class="card-body">
											<form>
												<div class="form-row">
													<div class="form-group col-md-6">
														<label for="inputFirstName">Kullanıcı Adı: </label>
														<input type="text" class="form-control" v-model="UserName" placeholder="First name">
													</div>
													<div class="form-group col-md-6">
														<label for="inputLastName">Kullanıcı Soyadı: </label>
														<input type="text" class="form-control" v-model="LastName" placeholder="Last name">
													</div>
												</div>
												<div class="form-group">
													<label for="inputEmail4">Tel</label>
													<input type="email" class="form-control" v-model="Tel" placeholder="Email">
												</div>
												<div class="form-group">
													<label for="inputAddress">Adresi</label>
													<input type="text" class="form-control" v-model="Adress" placeholder="1234 Main St">
												</div>
												
											
												<button type="button" class="btn btn-primary" @click="saveozel">Değişikleri Kaydet</button>
											</form>

										</div>
									</div>

								</div>
								<div class="tab-pane fade" id="password" role="tabpanel">
									<div class="card">
										<div class="card-body">
											<h5 class="card-title">Parola</h5>

											<form>
												<div class="form-group">
													<label for="inputPasswordCurrent">Mevcut Parola</label>
													<input type="password" class="form-control" v-model="mevcutparola">
												</div>
												<div class="form-group">
													<label for="inputPasswordNew">Yeni Parola</label>
													<input type="password" class="form-control" v-model="yeniparola">
												</div>
												<div class="form-group">
													<label for="inputPasswordNew2">Yeni Parolayı Doğrula</label>
													<input type="password" class="form-control" v-model="yeniparolatekrar">
												</div>
												<button type="button" class="btn btn-primary" @click="paroladegis">Değişikleri Kaydet</button>
											</form>

										</div>
									</div>
								</div>
							</div>
						</div>
					</div>

				</div>
			</main>
</template>

<script>
import  state from "../Helper/custom_state";
import Swal from "sweetalert2";
export default {
	data() {
    return {
      UserName: "",
      LastName: "",
      Biyografi:"",
      Tel:"",
	  Adress:"",
	  mevcutparola:"",
	  yeniparola:"",
	  yeniparolatekrar:""

    
    };
  },
  mounted () {
	 
    this.$http.post(`${state.ip}/api/getuser`,{
		Email:JSON.parse(localStorage.getItem("user")).Email
	})
      .then(response => {
		  
        this.UserName=response.data.FirstName;
      this.LastName=response.data.LastName;
      this.Biyografi=response.data.Biyografi;
      this.Tel=response.data.Tel;
	  this.Adress=response.data.Adress;
	  
      })
  },
  methods:{
	  savegenel(){
		  if(this.UserName.length>0&&this.Biyografi.length>0){
			   this.$http.post(`${state.ip}/api/savegenel`,{
		FirstName:this.UserName,
		Biyografi: this.Biyografi
	})
      .then(response => {
		  if(response.data.success)
		   Swal.fire("", response.data.msg, "success");
		  else
		 Swal.fire("", response.data.msg, "error");
	  
      })
		  }
		  else{
			  Swal.fire("", "Tüm alanlar doldurulmalıdır", "error");
		  }
	  },

	  saveozel(){
                
		  if(this.UserName.length>0&&this.LastName.length>0&&this.Tel.length>0&&this.Adress.length>0){
			   this.$http.post(`${state.ip}/api/saveozel`,{
		        FirstName:this.UserName,
		        LastName: this.LastName,
		        Tel:this.Tel,
		        Adress:this.Adress
	           })
      .then(response => {
		  console.log(response);
		  if(response.data.success)
		   Swal.fire("", response.data.msg, "success");
		  else
		 Swal.fire("", response.data.msg, "error");
	  
      })
		  }
		    else{
			  Swal.fire("", "Tüm alanlar doldurulmalıdır", "error");
		  }
	  
	  },

	  paroladegis(){
		 
               if(this.mevcutparola.length>0&&this.yeniparola.length>0&&this.yeniparolatekrar.length>0){
			  if(this.yeniparola!==this.yeniparolatekrar){
               Swal.fire("", "Şifreler eşleşmiyor", "error");
			  }
			  else{
               this.$http.post(`${state.ip}/api/paroladegis`,{
		        mevcutparola:this.mevcutparola,
		        yeniparola: this.yeniparola,
		       
		        
	           })
         .then(response => {
		 
		  if(response.data.success){
		  Swal.fire("", response.data.msg, "success"); 
		  localStorage.clear();
         location.reload();
		  }
		   
		  else
		 Swal.fire("", response.data.msg, "error");
	  
             })
		  }
		  }
		    else{
			  Swal.fire("", "Tüm alanlar doldurulmalıdır", "error");
		  }

	  }
  }

};
</script>

